import { AppComponent } from './../app.component';
import { OnInit, EventEmitter } from '@angular/core';
var MyFirstComponent = /** @class */ (function () {
    function MyFirstComponent(app) {
        this.app = app;
        this.messageChange = new EventEmitter();
        this.twowayChange = new EventEmitter();
    }
    Object.defineProperty(MyFirstComponent.prototype, "twoway", {
        set: function (value) {
            console.log('on inbound binding update:', value);
            this.$twoway = value;
        },
        enumerable: true,
        configurable: true
    });
    MyFirstComponent.prototype.ngOnInit = function () {
        console.log('first component init');
    };
    MyFirstComponent.prototype.onImageClick = function () {
        var i = Math.random();
        //this.app.setMessage(i.toFixed(12));
        this.messageChange.emit(i.toFixed(12));
        this.twowayChange.emit(i);
    };
    return MyFirstComponent;
}());
export { MyFirstComponent };
